import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { v1 as uniqueKey } from 'uuid';
import AppBar from '@material-ui/core/AppBar';
import {
  List,
  ListItem,
  Collapse,
  Tabs,
  Tab,
  Hidden,
  Typography,
} from '@material-ui/core';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import CloseIcon from '@material-ui/icons/Close';
import Link from '@material-ui/core/Link';
import { makeStyles } from '@material-ui/core/styles';
import { NavLink } from 'react-router-dom';
import Logo from '../images/icons/geOps_logo_md.svg';

const propTypes = {
  /**
   * Title
   */
  title: PropTypes.string.isRequired,

  /**
   * List tabs configuration.
   */
  tabs: PropTypes.arrayOf(
    PropTypes.shape({
      /**
       * The tab label.
       */
      label: PropTypes.string.isRequired,

      /**
       * href
       */
      href: PropTypes.string,

      /**
       * The to path
       */
      to: PropTypes.string,
    }),
  ),
};

const defaultProps = {
  tabs: [],
};

const useStyles = makeStyles((theme) => ({
  appBar: {
    [theme.breakpoints.down('sm')]: {
      padding: '0 20px',
    },
    display: 'flex',
    alignItems: 'center',
    padding: '0 70px',
    boxShadow: '0px 10px 15px #35353520',
    background: 'white',
    height: '100px',
  },
  brand: {
    display: 'flex',
    alignItems: 'center',
    fontSize: 22,
  },
  logo: {
    [theme.breakpoints.down('xs')]: {
      width: 100,
    },
    width: 120,
  },
  title: {
    [theme.breakpoints.down('xs')]: {
      fontSize: 15,
      marginLeft: 10,
    },
    fontSize: 22,
    color: theme.palette.text.primary,
    fontWeight: 500,
    marginLeft: 15,
    marginBottom: 2,
  },
  buttonCollapse: {
    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
    boxShadow: 'none',
  },
  menuList: {
    boxShadow: '0px 10px 15px #35353520',
    backgroundColor: 'white',
    '& .active': {
      fontWeight: 'bold',
      color: theme.colors.primaryGreen,
    },
    zIndex: 1200,
    padding: 0,
  },
  menuListItem: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
    height: 60,
    borderBottom: '1px solid #0000001f',
    '&.active': {
      borderBottomWidth: 2,
      borderBottomColor: theme.colors.primaryGreen,
    },
  },
  tabs: {
    [theme.breakpoints.down('sm')]: {
      display: 'none',
    },
    '& .active': {
      fontWeight: 'bold',
      color: theme.colors.primaryGreen,
    },
  },
  tab: {
    fontWeight: 400,
  },
  collapse: {
    position: 'sticky',
    top: 100,
    left: 'auto',
    right: 0,
    background: 'white',
    zIndex: 1100,
  },
}));

const selectInitialTab = (pathValue, tabs) => {
  const newTab = tabs.find((option) => option.to === pathValue);
  // Return route from URL, else return false (no tab selected)
  return newTab ? newTab.label : false;
};

const handleChange = (event, newValue, tabOptions, setValue) => {
  const tab = tabOptions.find((tabOption) => tabOption.label === newValue);
  if (tab.to) {
    setValue(newValue);
  }
};

const Header = ({ title, tabs }) => {
  const classes = useStyles();
  const [open, setMenuOpen] = useState(false);
  const path = window.location.pathname.split('/')[1];
  const [value, setValue] = useState(selectInitialTab(path, tabs));

  return (
    <>
      <AppBar position="sticky" className={classes.appBar}>
        <Link href="/" underline="none">
          <div className={classes.brand}>
            <img className={classes.logo} src={Logo} alt="Logo" />
            <Typography className={classes.title}>{title}</Typography>
          </div>
        </Link>

        {/* Show menu button only if there are more than one tab (when the tabs don't fit) */}
        {tabs.length > 1 && (
          <div className={classes.buttonCollapse}>
            <IconButton onClick={() => setMenuOpen(!open)}>
              {open ? <CloseIcon /> : <MenuIcon />}
            </IconButton>
          </div>
        )}

        {/* Hide tabs when there are more than one (when they don't fit) */}
        <Tabs
          className={tabs.length > 1 ? classes.tabs : undefined}
          value={value}
          onChange={(evt, newValue) =>
            handleChange(evt, newValue, tabs, setValue)
          }
          variant="fullWidth"
        >
          {tabs.map((option) => (
            <Tab
              key={uniqueKey()}
              value={option.label}
              label={option.label}
              component={option.href ? 'a' : NavLink}
              to={option.to}
              href={option.href}
              target={option.href ? '_blank' : null}
              rel={option.href ? 'noopener' : null}
              className={classes.tab}
            />
          ))}
        </Tabs>
      </AppBar>

      <Hidden mdUp>
        <div className={classes.collapse}>
          <Collapse in={open}>
            <List className={classes.menuList}>
              {tabs.map((option) => (
                <ListItem
                  key={uniqueKey()}
                  button
                  className={classes.menuListItem}
                  component={option.href ? 'a' : NavLink}
                  onClick={() => setMenuOpen(false)}
                  to={option.to}
                  href={option.href}
                  target={option.href ? '_blank' : null}
                  rel={option.href ? 'noopener' : null}
                >
                  {option.label}
                </ListItem>
              ))}
            </List>
          </Collapse>
        </div>
      </Hidden>
    </>
  );
};

Header.propTypes = propTypes;
Header.defaultProps = defaultProps;

export default Header;
